import { Colors, Lightning } from "@lightningjs/sdk";
import { FocusBoxTemplateSpec } from "../../../models/template-specs";
import { theme } from "../../../configs";
import FancyRect, { Shadow } from "./shadowBorder.js"

class FocusBox
  extends Lightning.Component<FocusBoxTemplateSpec>
  implements Lightning.Component.ImplementTemplateSpec<FocusBoxTemplateSpec>
{
  readonly Box = this.tag("Box")!;
  readonly Rect = this.tag("Rect")!;

  static override _template(): Lightning.Component.Template<FocusBoxTemplateSpec> {
    return {
      // OuterBox: {
      //   zIndex: 2,
      //   rect: true,
      //   shader: { type: Lightning.shaders.FadeOut, fade: 35 },
      //   color: theme.colors.orange,
      // },
      // CutOut: {
      //   zIndex: 2,
      //   rect: true,
      //   shader: {
      //     type: Lightning.shaders.RoundedRectangle,
      //     radius: 20,
      //     stroke: 1,
      //     strokeColor: theme.colors.orange,
      //   },
      //   color: theme.colors.black,
      // },
      Box: {
        zIndex: 3,
        rect: true,
        shader: {
          type: Lightning.shaders.RoundedRectangle,
          radius: 20,
          stroke: 5,
          strokeColor: theme.colors.orange,
        },
        color: Colors("transparent").get(),
      },
      Rect: {
        type: FancyRect,
        zIndex: 2,
        color: Colors("transparent").get(),
        radius: 20,
        alpha: 0.4,
        // strokeWidth: 5,
        // strokeColor: theme.colors.orange,
        // innerStrokeWidth: 5,
        // innerStrokeColor: theme.colors.orange,
        shadow: new Shadow(0, 0, 30, 5, theme.colors.orange),
      },
    };
  }

  set props(obj: { w: number; h: number }) {
    console.log("obj", obj);
    const { w, h } = obj;
    this.Box.patch({
      w: w,
      h: h,
    });
    this.Rect.patch({
      w: w - 20,
      h: h - 20,
      x: 10, y: 10,
    });
  }
}

export default FocusBox;
