import Lightning from "@lightningjs/core";
import FancyRectShader from "./shadowBorderShader";

// Uses the same properties as CSS shadows
export class Shadow {
  constructor(offsetX, offsetY, radius, spread, color) {
    this.offsetX = offsetX;
    this.offsetY = offsetY;
    this.radius = radius;
    this.spread = spread;
    this.color = color;
  }

  getShadowCoords() {
    return [this.offsetX, this.offsetY, this.radius, this.spread];
  }
}

export default class FancyRect extends Lightning.Component {
  constructor(stage, props) {
    super(stage);

    this.rect = true;
    this._color = 0xffffffff;

    Object.assign(this, props);
  }

  get src() {
    return super.src;
  }

  set src(v) {
    super.src = v;

    if (!v) {
      this.rect = true;
    }
  }

  get radius() {
    return this._radius;
  }

  set radius(v) {
    this._radius = v;
    this.updateShader();
  }

  get shadow() {
    return this._shadow;
  }

  set shadow(v) {
    this._shadow = v;
    this.updateShader();
  }

  get strokeWidth() {
    return this._strokeWidth;
  }

  set strokeWidth(v) {
    this._strokeWidth = v;
    this.updateShader();
  }

  get strokeColor() {
    return this._strokeColor;
  }

  set strokeColor(v) {
    this._strokeColor = v;
    this.updateShader();
  }

  get innerStrokeWidth() {
    return this._innerStrokeWidth;
  }

  set innerStrokeWidth(v) {
    this._innerStrokeWidth = v;
    this.updateShader();
  }

  get innerStrokeColor() {
    return this._innerStrokeColor;
  }

  set innerStrokeColor(v) {
    this._innerStrokeColor = v;
    this.updateShader();
  }

  themeChanged() {
    if (this.colorName) {
      this._onColorNameChanged();
    }

    this.updateShader();
  }

  _init() {
    this._onResize = this.updateShader;
  }

  _firstActive() {
    this.updateShader();
  }

  getShadowCoords() {
    if (!this.shadowName) {
      return [0, 0, 0, 0];
    }
    const shadow = theme().shadow[this.shadowName];

    return [shadow.offsetX, shadow.offsetY, shadow.radius, shadow.spread];
  }

  getNormalizedRadius() {
    let rad;

    if (!Array.isArray(this.radius)) {
      rad = [this.radius, this.radius, this.radius, this.radius];
    }

    if (this.w > 0 && this.h > 0) {
      const halfSize = Math.min(this.w, this.h) / 2;

      for (let i = 0; i < 4; i++) {
        rad[i] = Math.min(rad[i], halfSize);
      }
    }

    return rad;
  }

  updateShader() {
    if (!this.__initialized) {
      return;
    }

    const shader = {
      type: FancyRectShader,
      radius: this.getNormalizedRadius(),
    };

    if (this.shadow && this.shadow instanceof Shadow) {
      shader.shadowCoords = this.shadow.getShadowCoords();
      shader.shadowColor = this.shadow.color;
    }

    if (this.strokeWidth) {
      shader.stroke = this.strokeWidth;

      if (this.strokeColor) {
        shader.strokeColor = this.strokeColor;
      }
    }

    if (this.innerStrokeWidth && this.innerStrokeColor) {
      shader.innerStroke = this.innerStrokeWidth || 0;
      shader.innerStrokeColor = this.innerStrokeColor ?? undefined;
    }

    this.shader = shader;
  }
}
